import Song from '../components/Song.vue'
import SongListCover from '../components/SongListCover.vue'
export default {
  components: {
    Song,
    SongListCover
  },
  data () {
    return {
      songlist: []
    }
  },
  mounted () {
    this.$http({
      method: 'GET',
      url: './static/data/songlist.json'
    })
      .then(response => {
        this.songlist = response.data
      })
  },
  methods: {
    updateTitle (name) {
      this.$store.dispatch('updateTitle', name)
    }
  }
}



